import { Component } from '@angular/core';
import { ViewController,IonicPage, AlertController, NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-Unidadpago3-create',
  templateUrl: 'unidadpago3-create.html',
})
export class Unidadpago3CreatePage {

  public form                  : FormGroup;
  username: string = "";
  public idtipo: string="";
  public idtipo2: string="";
  public idnombre: string="";
  public estado		     : any;
  public idsede		     : any;
  public tipo		     : any;
  public niveltt		     : any;
  public nivelntt		     : any;
  public costo		     : any;
  public codigo                 : any;
  public idjornadatt: string="";
  public tipogrupo                 : any;
  public tipocurso                 : any;
  public idjornada               : any;
  public nivel                 : any;
  public vnivel  :any=[];
  public vidjornada  :any=[];
  idempresa:any;

  private _ID                  : String;
  public pageTitle           	: string;

  onSelectChangeB(selectedValue: any) {   this.idjornadatt=selectedValue; }


  constructor(  public alertCtrl: AlertController,public storage: Storage,public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
    });
     this.form = this._FB.group({
      codigo     : ['', Validators.required],
       nivel     : ['', Validators.required],
       tipo     : ['', Validators.required],
       idjornada     : ['', Validators.required],
       costo    : [''],
    
       estado     : ['', Validators.required]
      
     
      
       
     });

    
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void    
  {
    this.idtipo=this.navParams.data.record2.idtipo;
    this.idsede=this.navParams.data.record2.idsede;
    this.idtipo2=this.navParams.data.record2.idtipo2;
    this.idnombre=this.navParams.data.record2.idnombre;
    this.tipogrupo=this.navParams.data.record2.tipogrupo;
    this.tipocurso=this.navParams.data.record2.tipocurso;

             





    if(this.navParams.get("record")==null)
    {
      this.authService.getReg( this.idsede+'/'+ this.idsede,'unidadjornada3s').then((result) => {
        this.vidjornada=result;
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
    
  
  this.authService.getReg2b(this.navParams.data.record2.idtipo2+'/'+this.navParams.data.record2.idtipo2 ,'unidadnivel3s').then((result) => {
    this.vnivel=result;
  }, (err) => {  
  this.msj.showError3('Error',err.error);
  });   
  

    
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
    else {
       this._ID 				=	this.navParams.data.record._id;
       this.codigo		=	this.navParams.data.record.codigo;

       this.authService.getReg( this.idsede+'/'+ this.idsede,'unidadjornada3s').then((result) => {
        this.vidjornada=result;
        this.idjornada=this.navParams.data.record.jornada._id
        this.idjornadatt=this.navParams.data.record.jornada._id
      }, (err) => {  
      this.msj.showError3('Error',err.error);
    });
    
  
  this.authService.getReg2b(this.navParams.data.record2.idtipo2+'/'+this.navParams.data.record2.idtipo2 ,'unidadnivel3s').then((result) => {
    this.vnivel=result;
    this.nivel		=	this.navParams.data.record.nivel._id + ',' +	this.navParams.data.record.nivel.nombre;
  }, (err) => {  
  this.msj.showError3('Error',err.error);
  });   

  

     
       this.niveltt=this.navParams.data.record.nivel._id
       this.nivelntt=	this.navParams.data.record.nivel.nombre
       this.estado		=	this.navParams.data.record.estado;
     
       this.tipo		=	this.navParams.data.record.tipo;
       this.costo		=	this.navParams.data.record.costo;
     
     
    
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    }
  }

  

  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
     let   options       : any	     = { 
      idtipounidad : this.idtipo,  
      idunidadacademica : this.idtipo2,  
      tipogrupo:this.tipogrupo,
      tipocurso:this.tipocurso,
      codigo : this.form.controls['codigo'].value,
     nivel : this.niveltt,    
     nombre : this.nivelntt,    
     jornada:this.idjornadatt,
     tipo: this.form.controls['tipo'].value,
     estado: this.form.controls['estado'].value,
     costo: this.form.controls['costo'].value,
     operacion:'otro',
    
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' pagoes calusac'}
    };
      

     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'unidadpago3s').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'unidadpago3s').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }


   

  manageGallery2() : void
  {

                let select = {
                  title: 'Seleccione Niveles',
                  message: 'Niveles',    inputs: [],
                  buttons: [           {            text: 'Cancelar'     },    {       text: 'Crear niveles',handler: todo => { 
                      //buscamos
                  
                      

     

  let creat='Actualiza2';
  
     let   options       : any	     = { 
      idtipounidad : this.idtipo,  
      idunidadacademica : this.idtipo2,  
      tipogrupo:this.tipogrupo,
      tipocurso:this.tipocurso,
      codigo : this.form.controls['codigo'].value,
     nivel : this.niveltt,    
     nombre : this.nivelntt,    
     jornada:this.idjornadatt,
     tipo: this.form.controls['tipo'].value,
     estado: this.form.controls['estado'].value,
     costo: this.form.controls['costo'].value,
     operacion:'actualiza2',
     niveles:todo,
    
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' pagoes calusac'}
    };
         

      if(options){
        this.authService.createReg(this._ID,options,'unidadpago3s').then((result) => {
           if (!this.form.valid) { return; }
           
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }
                      


                    }  } ]
              }
               var aa:any;
                aa=this.vnivel;
                for (var i = 0; i < aa.length; i++) {
                  select.inputs.push({name: 'myInput', type: 'checkbox',label:aa[i].nombre,value:aa[i]._id + '°' + aa[i].codigo + '°' + aa[i].nombre});
              }
              let newAlert = this.alertCtrl.create(select);
              newAlert.present();
   

  





  }


  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }


  
  onSelectChange(selectedValue: any) {  
    var aa=selectedValue.split(',')
    this.niveltt=aa[0]
    this.nivelntt=aa[1]
  }

}



